/*-----------------------------------------------------------------------------------*/
/*  Type
/*-----------------------------------------------------------------------------------*/

@import url('https://fonts.googleapis.com/css?family=Raleway:400,600,900&display=swap');

$font-base: 'Raleway',
sans-serif;

$weight-text: 400;
$weight-title: 900;
$weight-title-small: 600;

// font-size
$fs-base: 18;
$scale-factor: 1.333; // Perfect fourth
$fs-medium: $fs-base * $scale-factor; //23.99 
$fs-title: ($fs-medium * $scale-factor) * $scale-factor; //42.63


body {
  font: {
    family: $font-base;
    weight: $weight-text;
  }

  // Transforme la valeur de base en px en REM
  @include font-size($fs-base); // => 1.125 REM

  color: $text-color;
  line-height: 1.8;
}

.title {
  line-height: 1.2;

  font: {
    weight: $weight-title;
  }

  @include font-size($fs-title);

  &--small {
    font: {
      weight: $weight-title-small;
    }

    margin-top: 0;
    @include font-size($fs-medium);
  }

  &--big {

    margin-top: 0;
    color: $c-blue;
    line-height: 1.1;
  }

  &__home {
    top: 40px;
  }

  span {
    display: block;
  }
}

.content__el--blue .title--big {
  color: $c-white;
}

.content__el--big .title--big {
  top: 40px;
}

strong {
  font-weight: $weight-title;
}

p {
  margin-bottom: 1.25em;
  max-width: 600px;
}

h1 {
  margin-top: 0;
}

h1,
h2,
h3,
h4,
h5 {
  margin: 2.75rem 0 1rem;
}
