/*-----------------------------------------------------------------------------------*/
/*  Transitions
/*-----------------------------------------------------------------------------------*/

.anim {
  width: 100%;
  position: relative;
  overflow: hidden;
  pointer-events: none;
  z-index: -1;
  opacity: 0;
  animation: 1.5s 0.5s fadeIn $bezier forwards;

  &--on {
    z-index: 10;

    .circles__el {
      transform: translateY(-200vh);
    }
  }
}

.animation {
  animation: ($anim-delay + 2s) fadeOut forwards;
}

.fade-out {
  animation: 0.5s 0s fadeOut2 forwards;
}

.circles {
  position: fixed;
  bottom: -76vh;
  left: 0;
  width: 100%;
  height: 100vh;


  &__el {
    margin: 10px;
    height: 25px;
    width: 25px;
    position: absolute;
    transition: 2s cubic-bezier(0.45, 0.03, 0.18, 0.98);


    div {
      border-radius: 50%;
      animation: 3s ease-in-out infinite alternate;
    }

    @for $i from 0 through $circles-nbr {
      $r: 25px + random(120);
      $colors: $c-grey, $c-blue;
      $norm: $i / $circles-nbr;

      &:nth-child(#{$i}) {
        top: random(100) * 1%;
        left: random(100) * 1%;
        transition-delay: (1 - $norm) * $anim-delay;

        div {
          width: $r;
          height: $r;
          background: nth($colors, ($i % 2) + 1);
          animation-name: float-#{$i};
        }
      }
    }
  }
}
