/*-----------------------------------------------------------------------------------*/
/*  Results
/*-----------------------------------------------------------------------------------*/

.results {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  @include font-size($fs-medium);
  line-height: 1.4;



  @include screen(mobile) {
    flex-direction: row;
    align-items: flex-end;
  }

  &__number {
    @include font-size(55);
  }

  &__send {
    @include font-size(30);
  }

  &__el {
    padding: 50px;
    height: 100%;
    transform: translateY(100vh);
    animation: 1.2s 0.5s slideIn $bezier forwards;

    @for $i from 1 through 3 {
      $norm: $i / -3;

      &:nth-child(#{$i}) {
        animation-delay: (- $norm) * 0.8s;
      }
    }

  }

  &__text {
    max-width: 300px;
    padding-bottom: 0;
    padding-right: 0;
    position: relative;

    @include screen('(max-width: 500px)') {
      margin-bottom: 300px;
    }

    &::after {
      @include pseudo(block, absolute, 'Ã°ÂŸÂ‘Â‡');
      @include font-size(50);
      bottom: -70px;
      left: 50%;
      transform: translateX(-50%);
    }
  }

  &--blue {
    background: $c-blue;
    color: $c-white;

    li {
      position: relative;
      padding: 10px 0;

      span {
        padding-left: 60px;
      }

      &::after {
        @include pseudo(block, absolute, '');
        height: 25px;
        width: 25px;
        left: 10px;
        top: 50%;
        transform: translateY(-50%);
      }
    }

    @for $i from 1 through 4 {
      li:nth-child(#{$i})::after {
        background: center / contain no-repeat url("/assets/images/socials/0#{$i}.svg");
      }
    }
  }

  &--white {
    background: $c-white;
    position: relative;
    left: 20px;
    top: 20px;
    z-index: 10;

  }
}

.text__blue {
  color: $c-blue;
  @include font-size($fs-base);
  font-weight: $weight-title-small;
}
