/*-----------------------------------------------------------------------------------*/
/*  Buttons
/*-----------------------------------------------------------------------------------*/


.link {
  position: relative;
  padding: 0 20px;
  margin-top: 60px;
  display: inline-block;
  align-self: flex-end;

  &::after,
  &::before {
    position: absolute;
    display: block;
  }

  &::before {
    content: '';
    height: 2px;
    width: 100%;
    background: $c-white;
    left: 50%;
    bottom: -2px;
    transform: translateX(-50%);
  }

  &::after {
    content: '>';
    color: $c-white;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
  }
}

.btn {
  &__language {
    padding-right: 27px;
    font-size: 20px;
    font-weight: $weight-title;
    position: relative;

