/*-----------------------------------------------------------------------------------*/
/*  Animations
/*-----------------------------------------------------------------------------------*/

@keyframes fadeOut {
  60% {
    opacity: 1;
  }

  80% {
    opacity: 0;
  }

  100% {
    opacity: 0;
  }
}

@keyframes fadeOut2 {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}

@keyframes fadeIn {

  to {
    opacity: 1;
  }
}

@keyframes textAnim {
  0% {
    opacity: 0;
    transform: translateX(-100px);
  }

  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes textAnim2 {
  0% {
    opacity: 0;
    transform: translateX(100px);
  }

  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes slideIn {
  to {
    transform: translateY(0);
  }
}

@for $i from 0 through $circles-nbr {
  $neg-pos: 1px, -1px;
  $value: 55;
  $number: 2;

  @keyframes float-#{$i} {
    0% {
      transform: translateY(nth($neg-pos, random($number)) * random($value)) translateX(nth($neg-pos, random($number)) * random($value));
    }

    100% {
      transform: translateY(nth($neg-pos, random($number)) * random($value)) translateX(nth($neg-pos, random($number)) * random($value));
    }
  }
}
