/*-----------------------------------------------------------------------------------*/
/*  Layout
/*-----------------------------------------------------------------------------------*/

.container {
  position: relative;
  width: 100%;
  height: 100%;
  max-width: 350px;
  padding-right: 15px;
  padding-left: 15px;

  @include push--auto;

  @include screen(mobile) {
    max-width: 500px;
  }

  @include screen(tablet) {
    max-width: 1000px;
  }
}

.section {
  height: 100vh;
  min-height: 750px;
}
