/*-----------------------------------------------------------------------------------*/
.dropdown {
  position: relative;

  &--active {
    .dropdown__content {
      opacity: 1;
    }

    .dropdown__el {
      overflow: visible;
      height: 42px;
    }

    .dropdown__btn::after {
      transform: rotate(180deg) translateY(10px);
    }
  }

  &__btn {
    padding: 20px;
    font-size: 20px;
    font-weight: $weight-title;
    position: relative;

    &::after {
      @include pseudo(block, absolute, 'Ã¢ÂŒÂ„');
      right: 5px;
      top: 20px;
      line-height: 1;
      transform-origin: bottom;
      transition: transform .25s ease;
    }
  }

  &__content {
    position: absolute;
    left: 50%;
    opacity: 0;
    transform: translateX(-50%);
    background-color: #f9f9f9;
    min-width: 80px;
    overflow: hidden;
    box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
    z-index: 1;
    transition: opacity .25s cubic-bezier(0.14, 0.1, 0.39, 1.27) .1s;


  }

  &__el {
    position: relative;
    height: 0;
    padding: 5px 10px;
    display: block;
    text-align: center;
    transition: height .25s cubic-bezier(0.14, 0.1, 0.39, 1.27) .1s,
      background-color .2s ease;

    &:last-child::before {
      @include pseudo(block, absolute, '');
      height: 1px;
      width: 100%;
      background: lighten($text-color, 80%);
      left: 0;
      top: 0;
    }

    &:hover {
      background: $c-grey;
    }

    a {
      display: block;
    }
  }
}
