/*-----------------------------------------------------------------------------------*/
/*  Forms
/*-----------------------------------------------------------------------------------*/


fieldset {
  padding: 0;
}

.form {
  margin-top: 20px;

  &__el {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    position: relative;

    @for $i from 1 through 4 {
      &:nth-child(#{$i}) .form__label {
        background: center / contain no-repeat url("/assets/images/socials/0#{$i}.svg");
      }
    }

    &:last-child {
      margin-bottom: 0;
    }

    // &::after {
    //   @include pseudo(block, absolute, '>');
    //   right: 0;
    // }
  }

  &__label {
    text-indent: 100%;
    white-space: nowrap;
    overflow: hidden;
    $size: 25px;
    height: $size;
    width: $size;
    margin-right: 15px;
  }

  &__input {
    padding: 10px 10px 10px 0;
    width: 100%;
    @include font-size(16);
    color: $c-white;
    outline: 0;
    background: none;
    border: none;
    border-bottom: 2px solid $c-white;

    @include placeholder($c-bg, 0.4);
  }
}
