/*-----------------------------------------------------------------------------------*/
/*  Homepage
/*-----------------------------------------------------------------------------------*/

body {
  background: $c-bg;
}

.content {
  display: flex;
  flex-direction: column;
  justify-content: center;


  width: 100%;

  @include screen(mobile) {
    flex-direction: row;
    @include absolute(top 50% left 50%);
    transform: translate(-50%, -50%);
  }

  &__el {
    display: flex;
    flex-direction: column;
    min-width: 320px;
    height: 100%;
    width: 50%;
    background: $c-white;
    padding: 80px 40px;
    transform: translateY(-100vh);
    // padding-top: 126px;
    animation: 1.2s 0.5s slideIn $bezier forwards;
    margin-top: 100px;

    @include screen(tablet) {
      margin-top: initial;
      @include relative(left 20px);
      padding: 80px;
    }

    h1,
    h2,
    p {
      transform: translateX(-100px);
      opacity: 0;
      animation: 0.6s 1.4s textAnim cubic-bezier(0, .57, .69, .87) forwards;
    }

    &:first-child {
      margin-top: 100px;

      @include screen(tablet) {
        margin-top: -46px;
      }
    }

    &--small {
      padding-top: 80px;
    }

    &--big {
      padding-top: 126px;
    }

    &--fix {

      margin-top: initial;

      @include screen(mobile) {
        margin-top: -46px - 16px !important;
      }
    }


    &--blue {
      background: $c-blue;
      color: $c-white;
      left: -20px;
      transform: translateY(100vh);
      animation: 1.2s 0.5s slideIn $bezier forwards;

      h1,
      h2,
      p,
      a,
      form {
        transform: translateX(100px);
        opacity: 0;
        animation: 0.6s 1.4s textAnim2 cubic-bezier(0, .57, .69, .87) forwards;
      }
    }
  }

  &__results {
    max-width: 800px;
    flex-direction: column;

    .title--big {
      margin-bottom: 80px;
      transform: translateX(-100px);
      opacity: 0;
      animation: 0.8s 1.05s textAnim cubic-bezier(0, .57, .69, .87) forwards;

      @include screen("(max-width: 500px)") {
        margin-top: 200px;
      }
    }
  }
}

.header {
  @include absolute(top 20px left 0);
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 2;
  width: 100%;
  padding: 0 80px;
  opacity: 0;
  animation: 1.5s 0.5s fadeIn $bezier forwards;

  @include screen(mobile) {
    top: 40px;
  }

  &__logo {
    height: 60px;
  }
}



::selection {
  color: $c-blue;
  background: $c-bg;
}
